














































































import LazyLoad from "vanilla-lazyload";
import Vue from "vue";
import { BaseContainer, BaseRow, BaseColumn } from "@/components";

interface Rating {
    Source: string;
    Value: string;
}

interface Result {
    Title: string;
    Year: string;
    Rated: string;
    Released: string;
    Runtime: string;
    Genre: string;
    Director: string;
    Writer: string;
    Actors: string;
    Plot: string;
    Language: string;
    Country: string;
    Awards: string;
    Poster: string;
    Ratings: Rating[];
    Metascore: string;
    imdbRating: string;
    imdbVotes: string;
    imdbID: string;
    Type: "series" | "movie" | "game";
    totalSeasons: string;
    Response: "True" | "False";
}

const result: Result = {
    Title: "",
    Year: "",
    Rated: "",
    Released: "",
    Runtime: "",
    Genre: "",
    Director: "",
    Writer: "",
    Actors: "",
    Plot: "",
    Language: "",
    Country: "",
    Awards: "",
    Poster: "",
    Ratings: [],
    Metascore: "",
    imdbRating: "",
    imdbVotes: "0",
    imdbID: "",
    Type: "movie",
    totalSeasons: "0",
    Response: "False",
};

export default Vue.extend({
    data() {
        return {
            result: result,
            loading: true,
        };
    },
    components: {
        BaseColumn,
        BaseContainer,
        BaseRow,
    },
    computed: {
        id() {
            return this.$router.currentRoute.params.id;
        },
        isSeries() {
            return this.result.Type === "series";
        },
        hasMovieRating() {
            return !["N/A", "Not Rated", "Approved"].includes(
                this.result.Rated
            );
        },
        durationInMinutes(): number {
            return Number(this.result.Runtime.replace(/[^\d]+/, ""));
        },
        duration(): string {
            if (this.durationInMinutes < 60) {
                return this.result.Runtime;
            } else {
                const hours = Math.trunc(this.durationInMinutes / 60);
                const minutes = Math.trunc(
                    (this.durationInMinutes / 60 - hours) * 60
                )
                    .toString()
                    .padStart(2, "0");
                return `${hours}h${minutes}`;
            }
        },
    },
    async created() {
        this.loading = true;

        const response = await fetch(
            `https://www.omdbapi.com/?i=${this.id}&apikey=c7e9e5e0&plot=full`
        );
        const result: Result = await response.json();

        this.result = result;

        this.loading = false;

        await this.$store.dispatch("updateLazyLoad");
    },
});
